import React, { useRef, useState, useEffect } from "react";
import { makeStyles } from '@material-ui/core/styles';
import * as d3 from "d3";
const url = 'https://raw.githubusercontent.com/fivethirtyeight/data/master/bad-drivers/bad-drivers.csv';


const InsuranceBarChart = ({ width }) => {

	let [data, setData] = useState([]);

	useEffect(() => {
		try {
			(async () => {
				let response = await d3.csv(url)

				await setData(response);
			})();

		} catch (error) {
			console.error(error);
		}

	}, []);

	const useStyles = makeStyles((theme) => ({
		margin: {
			margin: theme.spacing(1),
		},
		extendedIcon: {
			marginRight: theme.spacing(1),
		},
	}));

	const classes = useStyles();

	const [sort, setSort] = useState(true);

	data = sort
		? [...data].sort((a, b) => b['Car Insurance Premiums ($)'] - a['Car Insurance Premiums ($)'])
		: [...data];

	const insuranceBarChartRef = useRef()

	const margin = ({ top: 30, right: 0, bottom: 30, left: 30 })

	const barHeight = 25;

	const height = Math.ceil((data.length + 0.1) * barHeight) + margin.top + margin.bottom

	const maxVal = d3.max(data.map((d) => parseFloat(d['Car Insurance Premiums ($)'])));

	const x = d3.scaleLinear()
		.domain([200, maxVal])
		.range([0, width])

	const y = d3.scaleBand()
		.domain(d3.range(data.length))
		.rangeRound([margin.top, height - margin.bottom])
		.padding(0.1)

	const xAxis = g => g
		.attr("transform", `translate(${120},${margin.bottom - 10})`)
		.call(d3.axisTop(x).ticks(6))
		.call(g => g.select(".domain").remove())

	const yAxis = g => g
		.attr("transform", `translate(${margin.left + 80},0)`)
		.call(d3.axisLeft(y).tickFormat(i => {
			if (data[i].State === 'District of Columbia') {
				data[i].State = 'D.C.';
			}
			return data[i].State
		}).tickSizeOuter(0))

	const svg = d3.select(insuranceBarChartRef.current)
		.attr("viewBox", [0, 0, width, height]);

	svg.append("g")
		.attr("fill", "ForestGreen")
		.selectAll("rect")
		.data(data)
		.join("rect")
		.attr("x", x(3))
		.attr("y", (d, i) => y(i))
		.attr("width", d => x(d['Car Insurance Premiums ($)']) - x(0))
		.attr("height", y.bandwidth())
		.on("mouseover", function (d) {
			d3.select(this).style('fill', 'Red');
		}).on("mouseout", function (d) {
			d3.select(this).style('fill', 'ForestGreen');
		});

	svg.append("g")
		.attr("fill", "white")
		.attr("text-anchor", "end")
		.attr("font-family", `-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol"`)
		.attr("font-size", 16)
		.selectAll("text")
		.data(data)
		.join("text")
		.attr("x", d => x(d['Car Insurance Premiums ($)']))
		.attr("y", (d, i) => y(i) + y.bandwidth() / 2)
		.attr("dy", "0.35em")
		.attr("dx", -4)
		.text(d => `$${parseInt(d['Car Insurance Premiums ($)'])}`)
		.call(text => text.filter(d => x(d['Car Insurance Premiums ($)']) - x(0) < 20) // short bars
			.attr("dx", +4)
			.attr("fill", "black")
			.attr("text-anchor", "start"));

	svg.append("g")
		.call(xAxis);

	svg.append("g")
		.call(yAxis)
		.selectAll("text")
		.attr("fill", "white")
		.attr("font-family", `-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol"`)

		.attr("font-size", 13)

	return (
		<div>
			<svg ref={insuranceBarChartRef}></svg>
		</div>
	);
}

export default InsuranceBarChart;